@import url(https://fonts.googleapis.com/css?family=Coiny);
@import url(https://fonts.googleapis.com/css?family=Archivo+Black|Rubik);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  padding-top: 10px;

  background-color: #94618E;
  color:#F8EEE7;
}

.Navbar__title {
  font-family: 'Coiny', cursive;
  font-size: 30px;
}

.Navbar__icons {
  display: flex;
  width: 100px;
  justify-content: space-between;
  align-items: center;
}

.Navbar__icons a {
  color: inherit;
  text-decoration: none;
}

.Navbar__icons a :hover{
  color: #600473;
}

.Navbar__icon {
  height: 100%;
  width: 35px;
  padding: 5px;
}

.Content {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 40px;
}

.Face {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  margin-top: 30px;
  width: 100%;
}

.Eye {
  position: absolute;
}

.Pupil {
  position: absolute;
  background-color: black;
  border-radius: 5px;
}

.SectionsLinks {
  display: flex;
  width: 80%;
  min-width: 760px;
  justify-content: space-between;
}

.SectionLink {
  font-family: 'Archivo Black', sans-serif;
  color: #94618E;
  cursor: pointer;
  font-size: 35px;
  padding: 6px;
}

.SectionLink:hover{
  color: #600473;
}

.Section {
  padding: 20px 100px;
}

.Section__title {
  color: #94618E;
  font-size: 35px;
  padding: 10px 40px;
  font-family: 'Archivo Black', sans-serif;
}

.Section__content {
  font-size: 30px;
  color: #31004a;
  padding: 10px 40px;
  text-align: justify;
  font-family: 'Rubik', sans-serif;
}

.App {
  background-color: #f2f2f2;
}

